@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300&display=swap);
*{margin:10px;padding:0;font-family:'Source Code Pro', monospace;font-size:1rem;box-sizing:border-box}a{color:#fff}.page{padding:1rem}.header{display:flex;flex-direction:column;justify-content:space-between;align-items:center;margin-bottom:2rem;padding-bottom:0.5rem;border-bottom:1px solid #eee}.header__div{display:flex;justify-content:center;align-items:center}.header__img{display:flex;width:190px;margin:0}.header__title{font-size:1.5rem;color:black;text-align:center}.header__search{padding:0.5rem;width:15rem;border:2px solid #ddd;border-radius:0.5rem;background-color:#f8f8f8;outline:none;transition:all ease 0.3s}.header__search:focus{border-color:#8065d0;background-color:#fff}.form__label{margin:10px}.phrase__list{display:flex;justify-content:space-between;flex-wrap:wrap}.phrase__item{margin-bottom:1rem;padding:1rem;width:20rem;list-style:none;border-radius:0.5rem;border:1px solid #ddd;background-color:#8065d0;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px;font-weight:bold;color:#fff}.phrase__item>*{margin:3px 0}.phrase__span{color:#19181a}.phrase__label{display:inline-block;font-weight:normal;width:5rem;color:#fff}.phrase__name{font-weight:bold}.new-phrase__title{padding:3rem 0 1rem 0;font-size:1.5rem;color:#8065d0}.new-phrase__input{margin-right:1rem;margin-bottom:1rem;padding:0.5rem;width:10rem;border:2px solid #ddd;border-radius:0.5rem;background-color:#f8f8f8;outline:none;transition:all ease 0.3s}.new-phrase__input:focus{border-color:#8065d0;background-color:#fff;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px}.new-phrase__btn{padding:0.5rem 1.5rem;border-radius:0.5rem;border:none;border:2px solid #8065d0;background-color:#8065d0;font-weight:bold;color:#fff;cursor:pointer;transition:all ease 0.3s}.new-phrase__btn:hover{border-color:#8065d0;background-color:#8065d0}

